\n.home-card__button {\n  @include s1;\n  color: var(--accent-500)\n}\n","\n// Текст\n@mixin text($size: 18px, $weight: 400, $color: $black, $fontStyle: normal) {\n  font-size: $size;\n  font-weight: $weight;\n  font-style: $fontStyle;\n  color: $color;\n}\n\n// Неактивное состояние элемента\n@mixin disabled() {\n  cursor: default;\n  pointer-events: none;\n}\n\n// Визуально скрыть\n@mixin hidden {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 1px;\n  height: 1px;\n  clip: rect(1px, 1px, 1px, 1px);\n}\n\n// ------------------\n\n// Заголовки\n\n@mixin h {\n  font-style: normal;\n  font-weight: 700;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n}\n\n@mixin h1 {\n  @include h;\n  font-size: 36px;\n  line-height: 48px;\n}\n\n@mixin h2 {\n  @include h;\n  font-size: 32px;\n  line-height: 40px;\n}\n\n@mixin h3 {\n  @include h;\n  font-size: 30px;\n  line-height: 40px;\n}\n\n@mixin h4 {\n  @include h;\n  font-size: 26px;\n  line-height: 32px;\n}\n\n@mixin h5 {\n  @include h;\n  font-size: 22px;\n  line-height: 32px;\n}\n\n@mixin h6 {\n  @include h;\n  font-size: 18px;\n  line-height: 24px;\n}\n\n// Подзаголовки\n\n@mixin s {\n  font-style: normal;\n  font-weight: 500;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n  line-height: 24px;\n}\n\n@mixin s1 {\n  @include s;\n  font-size: 15px;\n}\n\n@mixin s2 {\n  @include s;\n  font-size: 13px;\n}\n\n// Тексты\n\n@mixin t {\n  font-style: normal;\n  font-weight: 400;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n}\n\n@mixin p1 {\n  @include t;\n  font-size: 15px;\n  line-height: 20px;\n}\n\n@mixin p2 {\n  @include t;\n  font-size: 13px;\n  line-height: 18px;\n}\n\n@mixin c1 {\n  @include t;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin c2 {\n  @include t;\n  font-weight: 500;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin label {\n  @include t;\n  font-weight: 700;\n  font-size: 12px;\n  line-height: 16px;\n  text-transform: uppercase;\n}\n\n// Кнопки\n\n@mixin button {\n 